import styled from 'styled-components';
import Item from './Item';
import { useState } from 'react';

const Wrapper = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
`;

interface ItemType {
  name: string;
  hidden: boolean;
  id: number;
}

const Items = () => {
  const [items, setItems] = useState<ItemType[]>([
    { name: 'QNAP / Site 1', hidden: true, id: 1 },
    { name: 'QNAP / Site 2', hidden: true, id: 2 },
    { name: 'QNAP / Site 3', hidden: true, id: 3 },
  ]);

  const updateItems = (id: number) =>{
    const newItems = items.map(item =>{
      if (item.id === id) {
        item.hidden = false
      }
      return item
    })
    setItems(newItems)
  }

  return (
    <Wrapper>
      {items.map((item: any) => {
        return <Item name={item.name} id={item.id} updateItems={updateItems} hidden={item.hidden} key={item.id} />;
      })}
    </Wrapper>
  );
};

export default Items;
