import styled from 'styled-components';
import Total from './Total';
import { useState } from 'react';

const Wrapper = styled.div`
  color: #fff;
  padding: 15px 0px;
  max-width: 400px;
`;

const Form = styled.form``;

const Button = styled.button`
  border-radius: 4px;
  background: #088482;
  color: #fff;
  border-color: transparent;
  padding: 4px 8px;
  width: 100px;

  :hover {
    cursor: pointer;
  }
`;

const Input = styled.input`
  background: #3f3f3f;
  border: 2px solid transparent;
  color: #fff;
  outline: none;
  font-size: 16px;
  width: 200px;

  :focus {
    border-color: #088482;
  }
`;

const List = styled.ul`
  list-style-type: none;
`;

const ListItem = styled.li`
  padding: 4px 0;
`;

const Label = styled.label`
  width: 500px;
  font-weight: 300;
  font-family: sans-serif;
  color: #b2b2b2;
`;

const Column = styled.div``;

const Row = styled.div`
  display: flex;
`;

const Title = styled.div`
  text-align: center;
  font-size: 20px;
  font-weight: 600;
`;

const Dropdown = styled.div`
  position: relative;
  display: inline-block;
  background: #3f3f3f;
  width: 200px;
  padding: 4px;

  :hover {
    cursor: pointer;
  }
`;

const DropdownText = styled.div`
  padding-left: 4px;
  width: 100%;
`

const DropdownContent = styled.div`
  position: absolute;
  background: #3f3f3f;
  width: 208px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  left: 0px;
  top: 24px;

  :hover {
    cursor: pointer;
  }
`;

const DropdownItem = styled.div`
  padding: 2px 8px;
  :hover {
    background: #088482;
    cursor: pointer;
  }
`;

interface ItemProps {
  name: string;
  id: number;
  updateItems: (id: number) => void;
  hidden: boolean;
}

const Item = ({ name, id, updateItems, hidden }: ItemProps) => {
  const [type, setType] = useState('Type');
  const [hours, setHours] = useState('Hours');
  const [typeDropdownOpen, setTypeDropdownOpen] = useState(false);
  const [hoursDropdownOpen, setHoursDropdownOpen] = useState(false);

  return (
    <Wrapper>
      <Title>{name}</Title>
      <Form>
        <List>
          <ListItem>
            <Row>
              <Column style={{ width: '40%' }}>
                <Label>Retention (days)</Label>
              </Column>
              <Column style={{ width: '60%' }}>
                <Input />
              </Column>
            </Row>
          </ListItem>
          <ListItem>
            <Row>
              <Column style={{ width: '40%' }}>
                <Label>Bitrate (Mbps)</Label>
              </Column>
              <Column style={{ width: '60%' }}>
                <Input />
              </Column>
            </Row>
          </ListItem>
          <ListItem>
            <Row>
              <Column style={{ width: '40%' }}>
                <Label># of Cameras</Label>
              </Column>
              <Column style={{ width: '60%' }}>
                <Input />
              </Column>
            </Row>
          </ListItem>
          <ListItem>
            <Row>
              <Column style={{ width: '40%' }}>
                <Label>Hrs/day Recording</Label>
              </Column>
              <Column style={{ width: '60%' }}>
                <Dropdown className="dropdown">
                  <DropdownText onClick={() => setHoursDropdownOpen(!hoursDropdownOpen)}>
                    {hours}
                    <div style={{ float: 'right', borderLeft: '1px solid #b2b2b2', padding: '0 6px'}}>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 407.437 407.437"
                        height="15px"
                        width="15px"
                      >
                        <path
                          fill="#fff"
                          stroke-width="10"
                          d="M386.258 91.567l-182.54 181.945L21.179 91.567 0 112.815 203.718 315.87l203.719-203.055z"
                        />
                      </svg>
                    </div>
                  </DropdownText>
                  <DropdownContent
                    className="dropdown-content"
                    style={{ display: hoursDropdownOpen ? 'block' : 'none' }}
                  >
                    <DropdownItem
                      style={{ background: hours === '12' ? '#088482' : '' }}
                      onClick={() => {
                        setHoursDropdownOpen(false);
                        setHours('12');
                      }}
                    >
                      12
                    </DropdownItem>
                    <DropdownItem
                      style={{ background: hours === '24' ? '#088482' : '' }}
                      onClick={() => {
                        setHours('24');
                        setHoursDropdownOpen(false);
                      }}
                    >
                      24
                    </DropdownItem>
                  </DropdownContent>
                </Dropdown>
              </Column>
            </Row>
          </ListItem>
          <ListItem>
            <Row>
              <Column style={{ width: '40%' }}>
                <Label>Desktop / Rack</Label>
              </Column>
              <Column style={{ width: '60%' }}>
                <Dropdown className="dropdown">
                  <DropdownText onClick={() => setTypeDropdownOpen(!typeDropdownOpen)}>
                    {type}
                    <div style={{ float: 'right', borderLeft: '1px solid #b2b2b2', padding: '0 6px'}}>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 407.437 407.437"
                        height="15px"
                        width="15px"
                      >
                        <path
                          fill="#fff"
                          stroke-width="10"
                          d="M386.258 91.567l-182.54 181.945L21.179 91.567 0 112.815 203.718 315.87l203.719-203.055z"
                        />
                      </svg>
                    </div>
                  </DropdownText>
                  <DropdownContent
                    className="dropdown-content"
                    style={{ display: typeDropdownOpen ? 'block' : 'none' }}
                  >
                    <DropdownItem
                      style={{ background: type === 'Desktop' ? '#088482' : '' }}
                      onClick={() => {
                        setType('Desktop');
                        setTypeDropdownOpen(false);
                      }}
                    >
                      Desktop
                    </DropdownItem>
                    <DropdownItem
                      style={{ background: type === 'Rack' ? '#088482' : '' }}
                      onClick={() => {
                        setType('Rack');
                        setTypeDropdownOpen(false);
                      }}
                    >
                      Rack
                    </DropdownItem>
                  </DropdownContent>
                </Dropdown>
              </Column>
            </Row>
          </ListItem>
          <ListItem>
            <Row>
              <Column style={{ width: '40%' }}>
                <Label>Upfront Payment</Label>
              </Column>
              <Column style={{ width: '60%' }}>
                <Input />
              </Column>
            </Row>
          </ListItem>
          <ListItem>
            <Row>
              <Column style={{ width: '40%' }}>
                <Label>Payback (months)</Label>
              </Column>
              <Column style={{ width: '60%' }}>
                <Input onChange={() => updateItems(id)} />
              </Column>
            </Row>
          </ListItem>
        </List>
      </Form>
      {!hidden && <Total />}
    </Wrapper>
  );
};

export default Item;

